.questions {
  padding: 120px 0 205px;

  &__title {
    margin-bottom: 7rem;
  }

  @include mw1 {
    padding: 80px 0 155px;

    &__title {
      margin-bottom: 5rem;
    }
  }

  @include mw3 {
    padding: 45px 0 95px;

    &__title {
      margin-bottom: 3.5rem;
    }
  }

  @include mw4 {
    padding: 30px 0 45px;

    &__title {
      margin-bottom: 2rem;
    }
  }
}

.accordion {
  &__item {
    position: relative;
    padding: 15px 55px;
    box-shadow: $shadow4;
    transition: box-shadow 0.3s linear;
    cursor: pointer;

    // question mark

    &::before {
      content: '?';
      position: absolute;
      left: 15px;
      top: 12px;
      border-radius: 50%;
      width: 24px;
      height: 24px;
      font-family: inherit;
      font-weight: 700;
      font-size: $fstiny;
      line-height: 24px;
      text-align: center;
      color: $seccolor;
      background-color: $thirdcolor1;
    }

    // chevron

    &::after {
      content: '';
      position: absolute;
      right: 15px;
      top: 20px;
      width: 10px;
      height: 10px;
      background-image: url('../images/svgSprite/svgSprite.svg#expand-arrow');
      background-position: center center;
      background-size: cover;
      background-repeat: no-repeat;
      object-fit: cover;
      transform: rotate(180deg);
      transition: transform 0.3s linear;
    }

    &:not(:last-child) {
      margin-bottom: 2rem;
    }
  }

  &__term {
    font-weight: 600;
    font-size: $fsSeminrml;
    line-height: 1.33;
  }

  &__definition {
    overflow: hidden;
    margin-top: 0;
    max-height: 0;
    opacity: 0;
    transition: max-height 0.3s linear, opacity 0.3s linear, margin 0.3s linear;
    will-change: max-height;
  }

  &__text {
    padding: 6px 0;
  }

  @include mw4 {
    &__item {
      padding: 10px 30px;

      &::before {
        left: 5px;
        top: 10px;
        width: 18px;
        height: 18px;
        font-size: $fsmicro;
        line-height: 18px;
      }

      &::after {
        top: 15px;
      }
    }
  }
}

// open

.open .accordion__definition {
  margin-top: 8px;
  opacity: 1;
}

.open.accordion__item {
  box-shadow: $shadow5;

  &::after {
    transform: rotate(0deg);
  }
}
