.header {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 15;
  width: 100%;
  min-height: 16.2rem;

  &__container {
    align-items: center;
    padding-top: 5rem;
    padding-bottom: 5rem;
    text-align: center;
    color: $seccolor;
  }

  &__logo {
    display: block;
    flex: 0 0 13rem;
    margin-right: 10px;
  }

  &__nav {
    flex: 1 1 auto;
    margin-right: 10px;
  }

  &__contacts {
    min-width: 19rem;
    max-width: 24rem;
  }

  // &.hide {
  //   top: -20%;
  //   visibility: hidden;
  // }
  &.fixed {
    position: fixed;
    transform: translate3d(0, -100%, 0);
  }

  &.vision {
    background-color: rgba($firstcolor, 0.6);
    transform: translate3d(0, 0, 0);
  }

  @include hd {
    min-height: 7.9rem;

    &__container {
      padding-top: 1rem;
      padding-bottom: 1rem;
    }
  }

  @include mw1 {
    &__container {
      padding-right: 65px;
    }

    &__logo {
      flex-basis: 9.5rem;
      margin-right: auto;
    }

    &__nav {
      position: fixed;
      left: 0;
      top: 0;
      z-index: 55;
      overflow-y: auto;
      margin-right: 0;
      padding: 4rem;
      width: 40%;
      height: 100vh;
      text-align: center;
      background-color: $thirdcol;
      visibility: hidden;
      transform: translate3d(-100%, 0, 0);
      transition: transform 0.2s linear, visibility 0.3s linear;

      &.active {
        visibility: visible;
        transform: translate3d(0%, 0, 0);
        transition: transform 0.2s linear, visibility 0.3s;
      }
    }

    &__burger {
      display: flex;
    }
  }

  @include mw3 {
    &__container {
      padding-right: 65px;
      min-height: 64px;
    }

    &__contacts {
      max-width: 38rem;
    }

    &__nav {
      width: 65%;
    }
  }

  @include mw4 {
    min-height: 6.4rem;

    &__nav {
      width: 80%;
    }
  }

  @include mw5 {
    &__nav {
      width: 100%;
    }

    &__contacts {
      justify-content: flex-end;
    }
  }
}

// mobile-version contacts, animanition button on click

.header__contacts {
  .contats__mobile {
    position: absolute;
    right: 60px;
    top: 10px;
  }

  &.active {
    .contacts__mobile {
      position: fixed;
      right: 65px;
      top: 10px;
      z-index: 57;

      &::before {
        box-shadow: inset 0 0 0 35px rgba(111, 148, 182, 0);
        animation: anim-click-open 0.4s ease-out forwards;
      }
    }

    .contacts__inner {
      opacity: 1;
      visibility: visible;
      transform: translate3d(0, 0, 0);
    }

    .contacts__social {
      justify-content: center;
    }
  }
}

