.consultation {
  position: relative;
  padding: 215px 0 172px;
  color: $seccolor;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: $thirdcolor1;

  &::before {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 1;
    background-color: rgba($firstcolor, 0.3);
  }

  &__inner {
    position: relative;
    z-index: 2;
    width: 100%;
    max-width: 60rem;
  }

  &__subtitle {
    margin-bottom: 3.5rem;
    line-height: 1.7;
  }

  &__field {
    &:not(:last-child) {
      margin-right: 1.5rem;
    }
  }

  &--bg1 {
    background-image: url('../images/consultation/main-bg.jpg');
  }

  &--bg2 {
    background-image: url('../images/consultation/main-bg2.jpg');
  }

  @include hd {
    padding: 175px 0 129px;
  }

  @include mw1 {
    padding: 130px 0 95px;
  }

  @include mw3 {
    padding: 105px 0 80px;

    &__inner {
      max-width: none;
    }
  }

  @include mw4 {
    padding: 70px 0 55px;

    &__subtitle {
      margin-bottom: 2.5rem;
    }

    &__fields {
      display: block;
    }

    &__field {
      &:not(:last-child) {
        margin-right: 0;
        margin-bottom: 1.5rem;
      }
    }

    &__button {
      width: 100%;
    }
  }
}
