
.advantages {
  &__item {
    flex: 1 0 calc(33.3333% - 15px);

    &:not(:last-child) {
      margin-right: 15px;
    }
  }

  @include mw1 {
    &__item {
      flex-basis: calc(33.3333% - 10px);

      &:not(:last-child) {
        margin-right: 10px;
      }
    }
  }

  @include mw3 {
    &__list {
      flex-wrap: wrap;
    }

    &__item {
      flex-basis: 100%;

      &:not(:last-child) {
        margin-right: 0;
        margin-bottom: 15px;
      }
    }
  }
}

.card-advantages {
  border: $border1;
  padding: 50px 30px 30px;

  &__icon {
    width: 6.4rem;
    height: 6.4rem;

    svg {
      width: 100%;
      height: 100%;
    }
  }

  &__text {
    font-weight: 500;
    font-size: $fsExtrnrml;
  }

  @include mw1 {
    padding: 35px 15px 20px;
  }

  @include mw3 {
    padding: 15px;

    &__icon {
      margin: auto auto 1rem;
      width: 5rem;
      height: 5rem;
    }

    &__text {
      font-size: $fsnrml;
      text-align: center;
    }
  }
}
